body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.app {
  max-width: 820px;
  margin: 28px auto;
  margin-bottom: 40px;

  .app-header {
    margin: 0 24px;

    .title {
      display: flex;
      justify-content: space-between;
      // align-items: flex-end;

      .title-text {
        font-size: 60px;
        font-weight: 600;
      }
      .reset-btn {
        color: #1677ff;
        cursor: pointer;
        &:hover {
          opacity: 0.7;
        }
      }
    }
    .description {
      color: #555;
      font-size: 20px;
      margin-top: 4px;
    }
    @media only screen and (max-width: 750px) {
      .title .title-text {
        font-size: 32px;
      }
      .description {
        color: #555;
        font-size: 16px;
      }
    }
  }

  .app-main {
    margin-top: 16px;
  }

  .user-list {
    display: flex;
    flex-wrap: wrap;
    // .user-item + .user-item {
    //   margin-top: 8px;
    // }
    .user-item {
      flex-basis: 25%;
      padding: 10px 20px;
      display: flex;

      .user-avatar {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        font-size: 20px;
        font-weight: bold;
        flex-shrink: 0;
        background: #7200da;
      }

      .user-right {
        // margin-left: 8px;
        margin: 2px 0px 2px 12px;

        .user-name {
          font-weight: bold;
          // font-size: 14px;
          color: #333;
        }

        .user-action {
          margin-top: 4px;

          .action-icon {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            background: #ddd;
            color: #333;
            cursor: pointer;
            transition: all 0.2s;
            font-size: 14px;
            font-weight: bold;

            &.active.work-report {
              background: #f9c00c;
              color: white;
            }
            &.active.wind {
              background: #00b9f1;
              color: white;
            }

            &:hover {
              opacity: 0.7;
            }
          }

          .action-icon + .action-icon {
            margin-left: 6px;
          }
        }
      }

      @media only screen and (max-width: 750px) {
        flex-basis: 33.3333%;
      }
      @media only screen and (max-width: 560px) {
        flex-basis: 50%;
        padding: 6px 20px;
        .user-avatar {
          width: 36px;
          height: 36px;
          font-size: 16px;
        }
        .user-right {
          margin-top: 8px;
          .user-action {
            .action-icon {
              width: 40px;
              height: 40px;
            }
            .action-icon + .action-icon {
              margin-left: 12px;
            }
          }
        }
      }
    }
  }

  .operation-area {
    margin: 20px 20px 0;
    .operation-item {
      cursor: pointer;
      transition: all 0.2s;
      &.active {
        color: #1677ff;
      }

      &:hover {
        opacity: 0.7;
      }
    }
    .operation-item + .operation-item {
      margin-left: 16px;
    }
    .show-user {
      margin-top: 12px;
      .show-user-name {
        display: inline-block;
        margin-right: 12px;
        color: #555;
      }
    }
  }
}
